@font-face {
    font-family: "icomoon";
    src: url("../css/fonts/icons/icomoon.eot?5ijpi9");
    src: url("../css/fonts/icons/icomoon.eot?5ijpi9#iefix")
            format("embedded-opentype"),
        url("../css/fonts/icons/icomoon.ttf?5ijpi9") format("truetype"),
        url("../css/fonts/icons/icomoon.woff?5ijpi9") format("woff"),
        url("../css/fonts/icons/icomoon.svg?5ijpi9#icomoon") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

