}

.fs1 {
    @apply col-span-3 py-4;

    [class^="icon-"],
    [class*=" icon-"] {
        font-size: 64px;
    }
}

/*--------------------------------------------------------------
    Social Icons - Style
--------------------------------------------------------------*/

.social-icons {
    justify-content: flex-start;

    ul {
        display: flex;
        justify-content: space-between;
        padding-right: 0px;
        margin-left: 0 !important;
        width: 100%;

        @screen lg {
            justify-content: center;
        }

        li {
            display: flex;
            margin-bottom: 0;
            width: 30px;
            padding-left: 0 !important;

            &:before {
                display: none;
            }

            @screen lg {
                width: 25px;
            }
        }
    }
    a {
        transition: all 300ms ease-out;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        i {
            color: black;
        }
        &:hover {
            i {
                color: red;
            }
        }
    }
}

